  text-align: left;
  transition: background 0.1s ease-out, color 0.1s ease-out;
  /**
   * Header link styles inside light theme
   */
  /**
   * Header link styles inside cozy theme
   */
  /**
   * Header link styles inside comfortable theme
   */
  margin: 0;
  padding-top: 0.6rem;
  padding-bottom: 0.5rem;
